import{useNavigation}from'@react-navigation/core';import{Page}from'@onekeyhq/components';import{EDemoRootRoutes}from"./Routes";function useDemoAppNavigation(){var navigation=useNavigation();var popStack=function(){navigation.getParent()?.goBack?.();};var pop=function(){if(navigation.canGoBack?.()){navigation.goBack?.();}else{popStack();}};var switchTab=function(route,params){navigation.navigate(EDemoRootRoutes.Main,{screen:route,params});};var pushModal=function(route,params){navigation.navigate(EDemoRootRoutes.Modal,{screen:route,params});};var pageHeaderReload=Page.Header.usePageHeaderReloadOptions();function setOptions(options){var reloadOptions=pageHeaderReload.reload(options);navigation.setOptions(reloadOptions);}return{navigation,reset:navigation.reset,dispatch:navigation.dispatch,push:navigation.navigate,switchTab,pushModal,pop,popStack,setOptions};}export default useDemoAppNavigation;